@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}


.landingPage {
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  padding-top: 10%;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 30px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

footer {
  margin-top: 30px;
  text-align: center;
}

* {
  box-sizing:border-box;
}

.kd-zero {
    margin: 0px;
    border: 0px;
    padding: 0px;
    height: 0px;
}

.kd-translate {
    -webkit-transform: translate(200px, 200px);
            transform: translate(200px, 200px);
}

.kd-hint {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
  }

/* Turn off parallax scrolling for tablets and phones */

@media only screen and (max-device-width: 1600px) {
    .bgimg-1, .bgimg-2, .bgimg-3 {
        background-attachment: scroll;
        min-height: 400px;
    }
}

/* Animations */

.kd-animate-opacity {-webkit-animation-name: appear;animation-name: appear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
@-webkit-keyframes appear {
  from {opacity:0.0};
  to {opacity:1.0}
 }
@keyframes appear {
  from {opacity:0.0};
  to {opacity:1.0}
 }

 .kd-animate-opacity-dis {-webkit-animation-name: disappear;animation-name: disappear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
@-webkit-keyframes disappear {
  from {opacity:1.0};
  to {opacity:0.0}
 }
@keyframes disappear {
  from {opacity:1.0};
  to {opacity:0.0}
 }
 
 .kd-animate-from-top {-webkit-animation-name: from-top;animation-name: from-top;
    position:absolute;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}
@-webkit-keyframes from-top {
  from {top:-100px}
  to {top:0px}
}
@keyframes from-top {
  from {top:-100px}
  to {top:0px}
}

.kd-animate-from-left {-webkit-animation-name: from-left;animation-name: from-left;
  position:absolute;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
@-webkit-keyframes from-left {
from {left:-310px}
to {left:0px}
}
@keyframes from-left {
from {left:-310px}
to {left:0px}
}


/* de tweede keer is nodig als je hetzelfde object nog een keer wilt animeren */

 .kd-animate-from-top-second-time {-webkit-animation-name: from-top-top-second-time;animation-name: from-top-top-second-time;
    position:absolute;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}
@-webkit-keyframes from-top-top-second-time {
  from {top:-100px}
  to {top:0px}
 }
@keyframes from-top-top-second-time {
  from {top:-100px}
  to {top:0px}
 }

