/* When the screen is less than 1000 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */

  @media screen and (max-width: 1920px) {
    .kd-button-top-bar a:not(:first-child) {display: none;}
    .kd-button-top-bar a.icon {
      float: right;
      display: block;
    }
    .kd-64-text, .kd-32-text {font-size: 16px;}
    }

    /* Hide and show classes */
  
    /* Mobile */
    @media (min-width: 0px) {
      .kd-mobile {display:inherit;}
      .kd-tablet {display:none;}
      .kd-small-screen {display:none;}
      .kd-large-screen {display:none};

      .kd-32-mtext {font-size:20px;}
      .kd-96-mtext {font-size:36px;}    
    }

    /* Tablet */

    @media (min-width: 688px) {
      .kd-mobile {display:none;}
      .kd-tablet {display:inherit;}
      .kd-small-screen {display:none;}
      .kd-large-screen {display:none};

      .kd-32-mtext {font-size:26px;}
      .kd-96-mtext {font-size:45px;}    
    }

    /* Small screen */

    @media (min-width: 992px) {
      .kd-mobile {display:none;}
      .kd-tablet {display:none;}
      .kd-small-screen {display:inherit;}
      .kd-large-screen {display:none};

      .kd-32-mtext {font-size:32px;}
      .kd-96-mtext {font-size:52px;}    
    }

    /* Large screen */

    @media (min-width: 1312px) {
      .kd-mobile {display:none;}
      .kd-tablet {display:none;}
      .kd-small-screen {display:none;}
      .kd-large-screen {display:inherit};

      .kd-32-mtext {font-size:32px;}
    }


