.kd-animate-down {
  overflow-x: hidden;
  -webkit-animation: animate-down infinite 1.5s;
          animation: animate-down infinite 1.5s;
}

@-webkit-keyframes animate-down {
  0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
  }

  40% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }

  60% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
  }
}

@keyframes animate-down {
  0%, 20%, 50%, 80%, 100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
  }

  40% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }

  60% {
        -webkit-transform: translateY(3px);
                transform: translateY(3px);
  }
}

.kd-animate-open-from-right {
  position: absolute;
  -webkit-animation: animate-open-from-right 1s;
          animation: animate-open-from-right 1s;
}

@-webkit-keyframes animate-open-from-right {
  from {-webkit-transform: translateX(0px);transform: translateX(0px)}
  to {-webkit-transform: translateX(-300px);transform: translateX(-300px)}
}

@keyframes animate-open-from-right {
  from {-webkit-transform: translateX(0px);transform: translateX(0px)}
  to {-webkit-transform: translateX(-300px);transform: translateX(-300px)}
}

.kd-animate-close-to-right {
  position: absolute;
  -webkit-animation: animate-close-to-right 1s;
          animation: animate-close-to-right 1s;
}

@-webkit-keyframes animate-close-to-right {
  from {-webkit-transform: translateX(0px);transform: translateX(0px)}
  to {-webkit-transform: translateX(30px);transform: translateX(30px)}
}

@keyframes animate-close-to-right {
  from {-webkit-transform: translateX(0px);transform: translateX(0px)}
  to {-webkit-transform: translateX(30px);transform: translateX(30px)}
}

